<template>
    <div class="login">
        <van-nav-bar right-text="先逛一逛" @click-right="goState('home')">
            <template #left>
                <div class="login-nav">
                    <div class="login-nav-icon">
                        <img class="auto-img" src="../../assets/common/icons/home_active.png" alt="icon">
                    </div>
                    <div class="login-nav-text">
                        <span>Yx Coffee</span>
                    </div>
                </div>
            </template>
        </van-nav-bar>

        <div class="login-user-box">
            <div class="user-box-title">
                <div class="box-title-welcome">
                    <span>欢迎回来!</span>
                </div>
                <div class="box-title-sub-title">
                    <span>Please login to your accounts</span>
                </div>
            </div>

            <van-form>
                <van-cell-group inset>
                    <van-field
                        v-model="userInfo.phone"
                        name="手机号"
                        label="手机号"
                        placeholder="手机号"
                        autocomplete="off"
                    />
                    <van-field
                        v-model="userInfo.password"
                        :type="isRegisterPassword ? 'password' : 'text'"
                        name="密码"
                        label="密码"
                        placeholder="密码必须为字母开头"
                        :right-icon="isRegisterPassword ? 'closed-eye' : 'eye-o'"
                        autocomplete="off"
                        @click-right-icon="toggleRegsiterPasswordType"
                    />
                </van-cell-group>
                <div class="login-user-box-forgot">
                    <span @click="goState('Forgot')">忘记密码?</span>
                </div>
                <div class="commit-btn">
                    <van-button round block color="#0C34BA" @click="login">
                        登录
                    </van-button>
                </div>
                <div class="commit-btn">
                    <van-button round block type="default" @click="toggleRegisterBox">
                        注册
                    </van-button>
                </div>
            </van-form>
        </div>

        <van-popup v-model:show="isShow" position="bottom" closeable>
            <div class="register-box">
                <div class="register-box-title">注册</div>
                <van-form>
                    <van-cell-group inset>
                        <van-field
                            v-model="userRegisterInfo.nickName"
                            name="昵称"
                            label="昵称"
                            placeholder="昵称"
                            autocomplete="off"
                        />

                        <van-field
                            v-model="userRegisterInfo.phone"
                            name="手机号"
                            label="手机号"
                            placeholder="11位手机号"
                            autocomplete="off"
                        />

                        <van-field
                            v-model="userRegisterInfo.password"
                            type="password"
                            name="密码"
                            label="密码"
                            :right-icon="isRegisterPassword ? 'closed-eye' : 'eye-o'"
                            placeholder="密码必须为字母开头"
                            autocomplete="off"
                            @click-right-icon="toggleRegsiterPasswordType"
                        />
                        <div class="commit-btn register-btn">
                            <van-button round block color="#0C34BA" @click="register">
                                注册
                            </van-button>
                        </div>
                    </van-cell-group>
                </van-form>
            </div>
            
        </van-popup>


    </div>
</template>

<script>
    export { default } from './login';
</script>

<style lang="less" scoped>
    @import './login.less';
</style>