import {reactive,toRefs,defineComponent} from 'vue';
import {useRouter} from 'vue-router';
import {Toast} from 'vant';
import {postRequest} from '../../request/api';
import url from '../../request/url';
import validate from '../../libs/validate';
import {token} from '../../libs/util';

export default defineComponent({
    name:'login',
    setup () {
        let router = useRouter();
        //data
        let state = reactive({
            userInfo: {
                phone: "",
                password: "",
            },
            userRegisterInfo: {
                nickName: "",
                phone: "",
                password: "",
            },
            isShow: false,
            isRegisterPassword: true,
        })
        //方法
        let goState = (name) => {
            router.push({name})
        };

        let toggleRegsiterPasswordType = () => {
            Reflect.set(state,'isRegisterPassword',!Reflect.get(state,'isRegisterPassword'))
        };

        let login = () => {
            let o = {
                phone: {
                    value: state.userInfo.phone,
                    errorMsg: "手机号格式不正确",
                    reg: /^1[3-9]\d{9}$/
                },
                password: {
                    value: state.userInfo.password,
                    errorMsg: "密码由数字字母下划线组合(6-16字符)",
                    reg: /^[A-Za-z]\w{5,15}$/
                },
            };

            let isPass = validate.valid(o);
            if(isPass){
                postRequest(url.login,{...state.userInfo}).then(({code,token:tokenString,msg}) => {
                    if(code == 200){
                        token('token',tokenString);
                        router.push({name:'home'})
                    }
                    Toast(msg)
                }).catch((err) => {
                    console.log("err==>",err);
                });
            }
        };

        let register = () => {
            let o = {
                nickName: {
                    value: state.userRegisterInfo.nickName,
                    errorMsg: "昵称由字母数字下划线汉字组合(1-10字符)",
                    reg: /^[\w\u4e00-\u9fa5]{1,10}$/
                },
                phone: {
                  value: state.userRegisterInfo.phone,
                  errorMsg: "手机号格式不正确",
                  reg: /^1[3-9]\d{9}$/
                },
                password: {
                    value: state.userRegisterInfo.password,
                    errorMsg: "密码由数字字母下划线组合(6-16字符)",
                    reg: /^[A-Za-z]\w{5,15}$/
                },
            };

            let isPass = validate.valid(o);
            if(isPass){
                postRequest(url.register,{...state.userRegisterInfo}).then(({code,msg}) => {
                    if(code == 100) Reflect.set(state,'isShow',false);
                    Toast(msg)
                }).catch((err) => {
                    console.log("err==>",err);
                });
            }

        };

        let toggleRegisterBox = () => {
            Reflect.set(state,'isShow',true)
        };

        return {
            ...toRefs(state),
            goState,
            toggleRegsiterPasswordType,
            login,
            register,
            toggleRegisterBox
        }
    }
})